.gl {
  width: 70%;
  margin: auto;
}

.PageTitle {
  font-size: 1rem;
  width: 70%;
  position: relative;
  display: flex;
  justify-content: left;
  margin: auto;
  padding: 50px;
  color: black;
}
.card{
  position: relative;
  flex: 1 1;
  max-width: 300px;
  height: 140px;
  background-color: #4d69b0;
  margin: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fIcon{
  font-size: 32px;
  color: white;
  transition: .3s linear;
}

.card:hover .icon{
  transform: scale(4);
  opacity: 0;
}

.card-content h3,
.card-content span{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 15px;
  opacity: 0;
}

.card-content h3{
  top: 20px;
  text-transform: uppercase;
  color: #11307c;
}

.card-content span{
  bottom: 20px;
  color: #fff;
  font-weight: 500;
}

.card:hover h3{
  opacity: 1;
  top: 46px;
  transition: .3s linear .3s;
}

.card:hover span{
  opacity: 1;
  bottom: 46px;
  transition: .3s linear .3s;
}

.container {
  width: 70%;
  margin: auto;
  padding: 50px;
}

.container-termine {
  width: 70%;
  margin: auto;
  padding: 50px;
  font-size: 1.2rem;
}

.container .box {
  width: 100%;
  padding: 30px;
  background: #fff;
  margin-top: 50px;
  border-radius: 10px;
  box-shadow: 0 0 10px -3px rgba(0, 0, 0, .1);
}

.container .box-img {
  width: 100%;
  padding: 10px;
  background: #fff;
  margin-top: 10px;
  margin-bottom: 2px;
  border-radius: 10px;
  box-shadow: 0 0 10px -3px rgba(0, 0, 0, .1);
}

.container h2 {
  margin-bottom: 40px;
}

@media screen and (max-width:1360px){
  .card{
      margin-right: auto;
      margin-left: auto;
      max-width: 80%;
      justify-content: center;
  }

  
}



@media (min-width:1360px) {
  .bodydiv{
    width: 60%;
    height: 50vh;
